* {
  margin: 0;
  padding: 0;
  cursor: default;
}

body {
  margin: 0;
  padding: 0;
  min-width: 320px;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input {
  width: 15vw;
  min-width: 100px;
}

.jumbotron {
  position: relative;
  background-color: rgba(0, 0, 0, .35) !important;
}

.bg-home {
  background-image: url('home.jpg');
}

.bg-about {
  background-image: url('about.jpg');
}

.bg-services {
  background-image: url('services.jpg');
}

.bg-contact {
  background-image: url('contact.jpg');
}

@media (max-width:800px) {
  .bg-home {
    background-image: url('home800.jpg');
  }

  .bg-about {
    background-image: url('about800.jpg');
  }

  .bg-services {
    background-image: url('services800.jpg');
  }

  .bg-contact {
    background-image: url('contact800.jpg');
  }
}

@media (max-width:400px) {
  .bg-home {
    background-image: url('home400.jpg');
  }

  .bg-about {
    background-image: url('about400.jpg');
  }

  .bg-services {
    background-image: url('services400.jpg');
  }

  .bg-contact {
    background-image: url('contact400.jpg');
  }
}

.jumbotron-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background-position: center;
  background-size: cover;
  z-index: -1;
}

.nav-logo {
  width: 8%;
}

.contact-logo {
  width: 25vw;
  min-width: 170px;
}

.page-hight-100 {
  height: 80vh;
}

.footCon {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.footCon-div {
  flex-wrap: nowrap;
}

.footCon-div-1 {
  width: 60%;
}



@media (max-width:780px) {

  .footCon {
    flex-direction: column;
  }

  .footCon-div {
    text-align: center;
  }

  .footCon-div-1 {
    width: 100%;
  }

  .input-group {
    justify-content: center;
  }

  .footCon-input {
    width: 30vh;
  }
}

/* SEKCJA KONTAKT */
.contact {
  display: flex;
}

.container-logo {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
}

.container-form {
  flex: 0 0 66.666667%;
  max-width: 66.666667%;
}

.form-contact {
  text-align: center;
}

.form-label {
  display: block;
  padding: 5px 0;
}

.form-input {
  margin: 0 5px;
  padding: 2px;
}

.form-area {
  margin: 10px auto;
  padding: 2px;
}

input#checkbox {
  width: auto;
  min-width: auto;
}

textarea {
  width: 30vw;
  min-width: 180px;
}

.red {
  color: crimson;
}

.green {
  color: green;
}

@media (max-width:599px) {
  .list-group-item {
    padding: 0.5rem .5rem !important;
  }

  .col-3 {
    max-width: 40% !important;
  }

  .col-9 {
    max-width: 60% !important;
  }

  .display-4 {
    font-size: 2.6rem !important;
  }

  .table td,
  .table th {
    padding: .75rem 0 !important;
  }

  .contact {
    display: block;
    text-align: center;
  }

  .container-logo,
  .container-form {
    flex: none;
    max-width: 100%;
  }
}

@media (max-width:767px) {
  .nav-logo {
    width: 18%;
  }

  span.navbar-brand {
    width: 50% !important;
  }
}